
import { defineComponent } from "vue";
import axios from "axios";
import { getCSSVariableValue } from "@/assets/ts/_utils";

export default defineComponent({
  name: "widget-1",
  props: {
    widgetClasses: String,
    height: Number,
  },
  watch: {
    selectedYear() {
      this.fetch();
    },
  },
  data() {
    return {
      options: [] as any,
      availableYears: [new Date().getFullYear()],
      selectedYear: 2021,
      series: [
        {
          name: "Analyse Environnementale",
          data: [0],
        },
        {
          name: "Plan d'actions",
          data: [0],
        },
      ],
    };
  },
  mounted() {
    this.options = this.createOptions();
    this.options.xaxis.categories = [
      "Janvier",
      "Février",
      "Mars",
      "Avril",
      "Mai",
      "Juin",
      "Juillet",
      "Aout",
      "Septembre",
      "Octobre",
      "Novembre",
      "Décembre",
    ];
    this.fetch();
  },
  methods: {
    createOptions() {
      const labelColor = getCSSVariableValue("--bs-gray-500");
      const borderColor = getCSSVariableValue("--bs-gray-200");
      const baseColor = getCSSVariableValue("--bs-warning");
      const secondaryColor = getCSSVariableValue("--bs-gray-300");
      return {
        chart: {
          fontFamily: "inherit",
          type: "bar",
          toolbar: {
            show: false,
          },
        },
        plotOptions: {
          bar: {
            horizontal: false,
            columnWidth: ["30%"],
            endingShape: "rounded",
          },
        },
        legend: {
          show: false,
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          show: true,
          width: 2,
          colors: ["transparent"],
        },
        xaxis: {
          categories: [],
          axisBorder: {
            show: false,
          },
          axisTicks: {
            show: false,
          },
          labels: {
            style: {
              colors: labelColor,
              fontSize: "12px",
            },
          },
        },
        yaxis: {
          labels: {
            style: {
              colors: labelColor,
              fontSize: "12px",
            },
          },
        },
        fill: {
          opacity: 1,
        },
        states: {
          normal: {
            filter: {
              type: "none",
              value: 0,
            },
          },
          hover: {
            filter: {
              type: "none",
              value: 0,
            },
          },
          active: {
            allowMultipleDataPointsSelection: false,
            filter: {
              type: "none",
              value: 0,
            },
          },
        },
        tooltip: {
          style: {
            fontSize: "12px",
          },
          y: {
            formatter: function (val) {
              return "" + val + "";
            },
          },
        },
        colors: [baseColor, secondaryColor],
        grid: {
          borderColor: borderColor,
          strokeDashArray: 4,
          yaxis: {
            lines: {
              show: true,
            },
          },
        },
      };
    },
    async fetch() {
      const response = await axios.get(
        window.location.origin + "/api/" + `charts/month/${this.selectedYear}`
      );
      // create an array of objects representing the counts for each month
      let countsByMonth = [
        { month: "Janvier", count: 0 },
        { month: "Février", count: 0 },
        { month: "Mars", count: 0 },
        { month: "Avril", count: 0 },
        { month: "Mai", count: 0 },
        { month: "Juin", count: 0 },
        { month: "Juillet", count: 0 },
        { month: "Aout", count: 0 },
        { month: "Septembre", count: 0 },
        { month: "Octobre", count: 0 },
        { month: "Novembre", count: 0 },
        { month: "Décembre", count: 0 },
      ];
      let countsByMonthPA = [
        { month: "Janvier", count: 0 },
        { month: "Février", count: 0 },
        { month: "Mars", count: 0 },
        { month: "Avril", count: 0 },
        { month: "Mai", count: 0 },
        { month: "Juin", count: 0 },
        { month: "Juillet", count: 0 },
        { month: "Aout", count: 0 },
        { month: "Septembre", count: 0 },
        { month: "Octobre", count: 0 },
        { month: "Novembre", count: 0 },
        { month: "Décembre", count: 0 },
      ];

      let cm_ae = countsByMonth;
      let pm_pa = countsByMonthPA;
      const analyseEnvCounts = response.data.analyseEnvCounts;
      const planActionCounts = response.data.planActionCounts;
      this.availableYears = response.data.years;

      analyseEnvCounts.forEach((ae) => {
        if (new Date(ae.month).getFullYear() == this.selectedYear) {
          const monthIndex = new Date(ae.month).getMonth();
          cm_ae[monthIndex].count = ae.count;
        }
      });

      planActionCounts.forEach((pa) => {
        if (new Date(pa.month).getFullYear() == this.selectedYear) {
          const monthIndex = new Date(pa.month).getMonth();
          pm_pa[monthIndex].count = pa.count;
        }
      });

      this.series[0].data = cm_ae.map((count) => count.count);
      this.series[1].data = pm_pa.map((count) => count.count);
      console.log(this.series);
    },
  },
});
