
import Dropdown3 from "@/components/dropdown/Dropdown3.vue";
import { defineComponent, ref, onMounted } from "vue";
import axios from "axios";
export default defineComponent({
  name: "widget-1",
  props: {
    widgetClasses: String,
    widgetColor: String,
    chartHeight: Number,
  },
  data() {
    return {
      items: [] as {
        icon: string;
        title: string;
        description: string;
        stats: number;
        color: string;
      }[],
    };
  },
  mounted() {
    this.fetch();
  },
  methods: {
    async fetch() {
      try {
        const response = await axios.get(
          // window.location.origin + "/analyse/count/priorite"
          window.location.origin + "/api/" + "analyse/count/s_ns"
        );
        this.items = response.data;
      } catch (error) {
        console.error(error);
      }
    },
  },
  computed: {
    total() {
      return this.items.reduce((acc, item) => acc + item.stats, 0);
    },
  },
});
