
import { defineComponent, onMounted } from "vue";
import MixedWidgetSNS from "@/components/widgets/mixed/Widget1CountSNS.vue";
import MixedWidgetNiveau from "@/components/widgets/mixed/Widget1CountNiveau.vue";
import MixedWidgetMaitrise from "@/components/widgets/mixed/Widget1CountMaitrise.vue";
import MixedWidget8Action from "@/components/widgets/mixed/Widget8Action.vue";
import { setCurrentPageBreadcrumbs } from "@/core/helpers/breadcrumb";
import ChartsWidget1 from "@/components/widgets/charts/Widget1.vue";
import ChartAnalyseWidget from "@/components/widgets/charts/AnalyseWidget.vue";

import axios from "axios";

export default defineComponent({
  components: {
    ChartAnalyseWidget,
    MixedWidgetNiveau,
    MixedWidgetSNS,
    MixedWidgetMaitrise,
    MixedWidget8Action,
  },
  name: "widgets-statistics",
  data() {
    return {
      progress: 0,
      count: 0,
    };
  },
  mounted() {
    this.fetch();
  },
  methods: {
    async fetch() {
      try {
        const response = await axios.get(
          window.location.origin + "/api/" + "actions/progress"
        );
        this.progress = response.data.average;
        this.count = response.data.count;
      } catch (error) {
        console.error(error);
      }
    },
  },
  setup() {
    onMounted(() => {
      setCurrentPageBreadcrumbs("Indicateurs", ["Analyse Environnementale"]);
    });
  },
});
